<template>
  <el-container class="container">
    <ul class="environment-scroll">
      <li v-for="(t, i) in roomList" :key="i">
        <el-card class="environment-item">
          <div style="font-size: 18px; font-weight: 600;">
            {{ t.name }}
          </div>
          <el-descriptions :column="3" style="margin-bottom: 20px;" :labelStyle="{'width': '5%','font-size': '11pt;'}" title="Devices" border>
            <el-descriptions-item :label="j+1" v-for="(d, j) in t.device" :key="j">{{ d }}</el-descriptions-item>
          </el-descriptions>
          <el-descriptions :column="3" style="margin-bottom: 20px;" :labelStyle="{'width': '5%','font-size': '11pt;'}" title="States" border>
            <el-descriptions-item :label="j+1" v-for="(d, j) in t.state" :key="j">{{ d }}</el-descriptions-item>
          </el-descriptions>
        </el-card>
      </li>
    </ul>
  </el-container>
</template>

<script>
import axios from 'axios'

export default {
  name: 'myEnvironment',
  components: {

  },
  data () {
    return {
      roomList: []
    }
  },
  methods: {
    getInfor () {
      const that = this
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(roomList => {
        for (let i = 0; i < roomList.data.length; i++) {
          const room = roomList.data[i]
          const roomDevice = []
          const roomState = []
          that.roomList.push({
            name: room == "Context" ? "Outer" : room,
            device: roomDevice,
            state: roomState
          })
          axios.get('/' + this.URL.url + '/room_device/' + room, {
            headers: {
              'Content-Type': 'application/text'
            }
          }).then(deviceList => {
            for (let j = 0; j < deviceList.data.length; j++) {
              const device = deviceList.data[j]
              that.roomList[i].device.push(device)
            }
            axios.get('/' + this.URL.url + '/room_state/' + room, {
              headers: {
                'Content-Type': 'application/text'
              }
            }).then(stateList => {
              for (let j = 0; j < stateList.data.length; j++) {
                const state = stateList.data[j]
                that.roomList[i].state.push(state)
              }
            }).catch(err => {
              console.log(err)
            })
          }).catch(err => {
            console.log(err)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    this.getInfor()
  }
}
</script>

<style>
.environment-item {
  margin: 5px;
  width: 98.5%;
}

.environment-scroll {
  list-style-type: none;
  overflow: auto;
  padding: 0;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
}
</style>
