<template>
  <div class="application-container">
    <div class="application-top">
      <el-button type="primary" plain size="small" icon="el-icon-plus" @click="addViolationRules" style="margin-right: 10px;margin-bottom: 10px;font-size: 12pt;padding: 5px 10px;">
        Add Violation Rules
      </el-button>
    </div>
    <div class="application-bottom" v-if="length === 0">
      <el-empty description="You do not have a defined application"></el-empty>
    </div>
    <div class="application-bottom" v-if="length !== 0">
      <ul class="scroll">
        <li v-for="(t, i) in applicationList" :key="i">
          <el-card class="application-item">
            <el-descriptions :labelStyle="{'width': '10%','font-size': '11pt;'}" :title="'Violation Rules ' + (i + 1)" :column="1" border>
              <!-- <template slot="extra">
                <el-button size="mini" icon="el-icon-edit" @click="updateApplication(t, i)">
                </el-button>
                <el-button size="mini" icon="el-icon-delete" @click="removeApplication(i)"></el-button>
              </template> -->
              <el-descriptions-item label="Name">{{ t.app }}</el-descriptions-item>
              <el-descriptions-item label="Description">
                <el-descriptions v-for="(property, j) in t.dsl" :key="j" :contentStyle="{'width': '35%','font-size': '11pt;'}" :labelStyle="{'width': '15%','font-size': '11pt;'}" :column="2" border>
                  <el-descriptions-item label="Violation State">{{ property.object }}</el-descriptions-item>
                  <el-descriptions-item label="Target State">{{ property.fix }}</el-descriptions-item>
                </el-descriptions>
              </el-descriptions-item>
              <!-- <el-descriptions-item label="Time">{{ t.time }}</el-descriptions-item> -->
            </el-descriptions>
          </el-card>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'violationList',
  components: {
  },
  data () {
    return {
      applicationList: '',
      length: 0
    }
  },
  methods: {
    getApplicaion () {
      const that = this
      axios({
        url: '/upload/findAll/' + this.URL.param,
        method: 'GET'
      }).then((response) => {
        that.applicationList = response.data
        console.log(response.data)
        that.length = response.data.length
      })
    },
    addViolationRules () {
      if (this.TIME.timeFlag === false) {
        this.TIME.timeFlag = true
        this.TIME.time = new Date().getTime()
      }
      this.$router.push({
        name: 'addViolationRules',
        params: {
          value: ''
        }
      })
    }
    // updateApplication (Info, index) {
    //   if (Info && index >= 0) {
    //     const item = JSON.parse(JSON.stringify(Info))
    //     this.$router.push({
    //       name: 'addViolationRules',
    //       params: {
    //         value: item
    //       }
    //     })
    //   }
    // },
    // removeApplication (index) {
    //   this.applicationList.splice(index, 1)
    //   this.$forceUpdate()
    // }
  },
  mounted () {
    this.getApplicaion()
  }
}
</script>

<style scoped>
.application-container {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
}

.application-top {
  width: 100%;
  height: 8%;
  margin-top: 2%;
  display: flex;
  justify-content: end;
}

.application-bottom {
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
}

.application-item {
  margin: 5px;
  width: 98.5%;
}

.scroll {
  list-style-type: none;
  overflow: auto;
  padding: 0;
  margin-top: 5px;
  margin-bottom: 5px;
}

.scroll::-webkit-scrollbar {
  display: none;
}
</style>
