import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
import axios from 'axios'
import VueAxios from 'vue-axios'
import state from '../public/global/state'
import url from '../public/global/url'
import time from '../public/global/time'

Vue.prototype.$labapi = 'labapi' // 本地开发，开启反向代理
Vue.prototype.$homeapi = 'homeapi' // 本地开发，开启反向代理
Vue.prototype.$upload = 'upload' // 本地开发，开启反向代理
Vue.config.productionTip = false
Vue.use(VueAxios, axios)
Vue.prototype.STATE = state
Vue.prototype.URL = url
Vue.prototype.TIME = time

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
