<template>
  <el-container class="container">
    <el-main>
      <h1 class="title">Add Violation Rules</h1>
      <div style="display: flex;flex-direction: row-reverse;margin-top: 20px;">
        <el-button type="primary" @click="checkUpload" style="margin-right: 30px;margin-bottom: 10px;">UPLOAD</el-button>
      </div>
      <el-dialog width="70%" title="UPLOAD CONFIGURATION" :visible.sync="uploadDialogVisible">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="Name" prop="name">
            <el-input placeholder="Please input the name of the violation description" v-model="form.name" clearable>
            </el-input>
          </el-form-item>
          <el-form-item label="User" prop="user">
            <el-input placeholder="Please input your nickname" v-model="form.user" clearable>
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="uploadDialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="upload">Confirm</el-button>
        </span>
      </el-dialog>
      <el-row :gutter='20' type="flex">
        <el-col :span='9'>
          <div class='state-title' style="font-weight: bold;">
            Violation States
          </div>
          <!-- list 配置结果-->
          <div v-for="(t, i) in states" :key="i">
            <el-card class="card-margin">
              <el-descriptions :labelStyle="{'width': '18%','font-size': '11pt'}" :title="'Violation State ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
                </template>
                <el-descriptions-item label="location">{{ t.location}}</el-descriptions-item>
                <el-descriptions-item label="state">{{ t.state  + '.' + t.value}}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!-- 添加按钮和配置对话框 -->
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addState()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
            </el-button>
            <el-dialog title="State" :visible.sync="stateDialog" @close="cancelAddState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="stateLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="Category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category" @change="stateCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="State" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" style="width: 120px;margin-right:20px" @change="statesChange" v-model="statesTemplate.state"
                    placeholder="Please Choose the Device">
                    <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" style="width: 120px;margin-right:20px" @change="statesChange" v-model="statesTemplate.state" placeholder="Please Choose the State">
                    <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddState">Cancel</el-button>
                <el-button type="primary" @click="confimAddState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
        </el-col>
        <el-col :span='6'>
          <!-- 
          <div class='frequency-title' style="font-weight: bold;">
            Frequency
          </div>
          -->
          <div style="margin: auto auto;font-size:15pt;width:80%">
            <!--
            <el-select v-model="radio" style="width: 70%;" placeholder="Frequency" class="radio">
              <el-option v-for="item in radioOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            -->
            <div style="font-size:15pt;margin-top:40%;font-weight: bold;">
              Should be Handled as
            </div>
          </div>
        </el-col>
        <el-col :span='9'>
          <div class='condition-title' style="font-weight: bold;">
            Target States
          </div>
          <div v-for="(t, i) in violationStates" :key="i">
            <el-card class="card-margin">
              <el-descriptions :labelStyle="{'width': '18%','font-size': '11pt'}" :title="'Target State ' + (i + 1)" :column="1" border>
                <el-descriptions-item label="state">{{ t.location + '.' + t.state}}</el-descriptions-item>
                <el-descriptions-item label="value">
                  <el-select v-model="t.value" lable="13" placeholder="Please Choose the Target Value">
                    <el-option v-for="item in [{ value: '', label: 'None' }, ...myState.state[t.state]]" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <!-- list 配置结果-->
          <!--
          <div v-for="(t, i) in conditions" :key="i">
            <el-card class="card-margin">
              <el-descriptions :labelStyle="{'width': '16%','font-size': '11pt'}" :title="'Filter ' + (i + 1)" :column="1" border>
                <template slot="extra">
                  <el-button size="mini" icon="el-icon-edit" @click="updateConditionItem(t, i)">
                  </el-button>
                  <el-button size="mini" icon="el-icon-delete" @click="removeConditionItem(i)"></el-button>
                </template>
                <el-descriptions-item v-if="t.time === '' || t.time === null" label="Time">{{ t.way }}</el-descriptions-item>
                <el-descriptions-item v-else label="Time">{{ t.way + ' ' + t.time  + ' ' + t.timeUnit }}</el-descriptions-item>
                <el-descriptions-item label="State">{{ t.state }}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </div>
          <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
            <el-button type="text" @click="addCondition()">
              <i class="el-icon-circle-plus" style="font-size: 25px;"> Add Filter</i>
            </el-button>
            <el-dialog title="Filter" :visible.sync="conditionDialog" @close="cancelAddCondition">
              <el-form center width="50%" class="commit-form" ref="conditionsTemplate" label-width="15%" :model="conditionsTemplate" :rules="condition_rules">
                <el-form-item label="Way" prop="way">
                  <el-select v-model="conditionsTemplate.way" placeholder="Please Select" @change="wayChange">
                    <el-option v-for="item in wayOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="conditionsTemplate.way === 'Last'" label="Time" prop="time">
                  <span>
                    <el-input-number v-model="conditionsTemplate.time" controls-position="right" :min="1" style="width: 30%;margin-right:5%">
                    </el-input-number>
                  </span>
                  <el-select v-model="conditionsTemplate.timeUnit" placeholder="Minimum/Maximum Duration" style="width: 50%">
                    <el-option label="min" value="min"></el-option>
                    <el-option label="hour" value="hour"></el-option>
                    <el-option label="day" value="day"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item v-if="conditionsTemplate.way === 'While'" label="State" prop="state">
                  <div class="consition-state">
                    {{conditionsTemplate.state}}
                    <div style="float: right;margin-right:5px;">
                      <el-button size="mini" icon="el-icon-edit" @click="editConditionState()">
                      </el-button>
                    </div>
                  </div>
                </el-form-item>
                <el-form-item v-if="conditionsTemplate.way === 'After' || conditionsTemplate.way === 'Before'" label="State" prop="state">
                  <el-switch v-model="conditionsTemplate.conditionStatesFlag" @change="conditionChooseState"></el-switch>
                  <div v-if="conditionsTemplate.conditionStatesFlag" class="consition-state">
                    {{conditionsTemplate.state}}
                    <div style="float: right;margin-right:5px;">
                      <el-button size="mini" icon="el-icon-edit" @click="editConditionState()">
                      </el-button>
                    </div>
                  </div>
                </el-form-item>
                <el-form-item v-if="conditionsTemplate.way === 'After' || conditionsTemplate.way === 'Before'" label="Time" prop="time">
                  <div v-if="conditionsTemplate.conditionStatesFlag">
                    <el-switch v-model="conditionsTemplate.conditionTimeFlag" @change="conditionChooseTime"></el-switch>
                    <div v-if="conditionsTemplate.conditionTimeFlag" class="time-state">
                      <span>
                        <el-input-number v-model="conditionsTemplate.time" controls-position="right" :min="1" style="width: 30%;margin-right:5%">
                        </el-input-number>
                      </span>
                      <el-select v-model="conditionsTemplate.timeUnit" placeholder="Minimum/Maximum Duration" style="width: 50%">
                        <el-option label="min" value="min"></el-option>
                        <el-option label="hour" value="hour"></el-option>
                        <el-option label="day" value="day"></el-option>
                      </el-select>
                    </div>
                  </div>
                  <div v-else>
                    <el-switch v-model="conditionsTemplate.conditionTimeFlag" @change="conditionChooseTime"></el-switch>
                    <el-time-picker v-if="conditionsTemplate.conditionTimeFlag" format="HH:mm" value-format="HH:mm" placeholder="Time" v-model="conditionsTemplate.time"
                      style="width: 100%;"></el-time-picker>
                  </div>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddCondition">Cancel</el-button>
                <el-button type="primary" @click="confimAddCondition">Confirm</el-button>
              </div>
            </el-dialog>

            <el-dialog title="Edit States" :visible.sync="conditionStatesDialog" @close="cancelEditConditionStates">
              <div v-for="(t, i) in conditionStates" :key="i">
                <el-card class="card-margin">
                  <el-descriptions :labelStyle="{'width': '15%','font-size': '12pt'}" :title="'State ' + (i + 1)" :column="1" border>
                    <template slot="extra">
                      <el-button size="mini" icon="el-icon-edit" @click="updateConditionStateItem(t, i)">
                      </el-button>
                      <el-button size="mini" icon="el-icon-delete" @click="removeConditionStateItem(i)"></el-button>
                    </template>
                    <el-descriptions-item label="state">{{ t.location + '.' + t.category + '.' + t.state  + '.' + t.value}}</el-descriptions-item>
                  </el-descriptions>
                </el-card>
              </div>
              <el-button type="text" @click="addConditionState()">
                <i class="el-icon-circle-plus" style="font-size: 25px;"> Add State</i>
              </el-button>
              <div slot="footer">
                <el-button @click="cancelEditConditionStates">Cancel</el-button>
                <el-button type="primary" @click="confimEditConditionStates">Confirm</el-button>
              </div>
            </el-dialog>

            <el-dialog title="State" :visible.sync="conditionStateDialog" @close="cancelAddConditionState">
              <el-form ref="statesTemplate" :model="statesTemplate" label-width="20%" :rules="state_rules">
                <el-form-item label="Location" prop="location">
                  <el-select v-model="statesTemplate.location" placeholder="Please Choose the Location" @change="stateLocationChange">
                    <el-option v-for="item in locationOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="Category" prop="category">
                  <el-select v-model="statesTemplate.category" placeholder="Please Choose the Category" @change="stateCategoryChange">
                    <el-option label="device" value="device"></el-option>
                    <el-option label="state" value="state"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="State" prop="state">
                  <el-select v-if="statesTemplate.category=='device'" style="width: 120px;margin-right:20px" @change="statesChange" v-model="statesTemplate.state"
                    placeholder="Please Choose the Device">
                    <el-option v-for="item in deviceOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-if="statesTemplate.category=='state'" style="width: 120px;margin-right:20px" @change="statesChange" v-model="statesTemplate.state" placeholder="Please Choose the State">
                    <el-option v-for="item in stateOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <el-select v-model="statesTemplate.value" placeholder="Please Choose the Value">
                    <el-option v-for="item in valueOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer">
                <el-button @click="cancelAddConditionState">Cancel</el-button>
                <el-button type="primary" @click="confimAddConditionState">Confirm</el-button>
              </div>
            </el-dialog>
          </el-card>
          -->

        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
// import { radioOptions, wayOptions } from '../../public/global/data.js'
export default {
  name: 'addViolationRules',
  components: {
  },
  data () {
    return {
      // radioOptions,
      // wayOptions,
      stateDialog: false,
      stateUpdateFlag: false,
      states: [],
      violationStates: [],
      myState: this.$root.STATE,
      form: {
        name: '',
        user: '',
      },
      rules: {
        name: [
          { required: true, message: 'Please input the name of the violation description!', trigger: 'blur' }
        ],
        user: [
          { required: true, message: 'Please input your nickname!', trigger: 'blur' }
        ]
      },

      uploadDialogVisible: false,
      /*
      conditionDialog: false,
      conditionStatesDialog: false,
      conditionStateUpdateFlag: false,
      conditionStateDialog: false,
      conditionStates: [],
      conditions: [],
      conditionUpdateFlag: false,
      conditionsTemplate: {
        way: '',
        state: '',
        time: '',
        timeUnit: '',
        conditionStatesFlag: false,
        conditionTimeFlag: false
      },
      */
      statesTemplate: {
        category: '',
        state: '',
        value: '',
        location: ''
      },
      violationStatesTemplate: {
        category: '',
        state: '',
        value: '',
        location: ''
      },
      state_rules: {
        location: [
          { required: true, message: 'please select the location.', trigger: 'blur' }
        ],
        category: [
          { required: true, message: 'please select the category.', trigger: 'blur' }
        ],
        state: [
          { required: true, message: 'Please select the state.', trigger: 'blur' }
        ]
      },
      /*
      condition_rules: {
        way: [
          { required: true, message: 'please select the way of this condition!', trigger: 'blur' }
        ]
      },
      radio: 'Always',
      */
      propertyList: [],
      stateOptions: [],
      locationOptions: [],
      deviceOptions: [],
      actionOptions: [],
      valueOptions: [],
      index: '',
      // conditionIndex: '',
      // conditionStateIndex: ''
    }
  },
  mounted () {
    this.getLocation()
  },
  methods: {
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.stateDialog = true
        this.statesTemplate = item
        this.inquiryInfor(this.statesTemplate.category)
        this.statesChange(this.statesTemplate.state)
        this.index = index
        this.stateUpdateFlag = true
      }
    },
    removeItem (index) {
      this.states.splice(index, 1)
      this.violationStates.splice(index, 1)
      this.refresh()
    },
    inquiryInfor (val) {
      const that = this
      if (val === 'device') {
        this.deviceOptions = []
        axios.get('/' + this.URL.url + '/room_device/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            const data = res.data[i].slice(0, -3)
            // var temp = data[0:-3]
            that.deviceOptions.push({
              value: data,
              label: data
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else if (val === 'state') {
        this.stateOptions = []
        axios.get('/' + this.URL.url + '/room_state/' + that.statesTemplate.location, {
          headers: {
            'Content-Type': 'application/text'
          }
        }).then(res => {
          for (let i = 0; i < res.data.length; i++) {
            that.stateOptions.push({
              value: res.data[i],
              label: res.data[i]
            })
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    stateLocationChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(this.statesTemplate.category)
    },
    stateCategoryChange (val) {
      this.statesTemplate.state = null
      this.statesTemplate.value = null
      this.inquiryInfor(val)
    },
    addState () {
      this.stateDialog = true
      this.stateUpdateFlag = false
    },
    cancelAddState () {
      this.stateDialog = false
      this.statesTemplate = {
        category: '',
        state: '',
        location: '',
        value: ''
      }
      this.violationStatesTemplate = {
        category: '',
        state: '',
        location: '',
        value: ''
      }
      this.refresh()
    },
    confimAddState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.stateDialog = false
          this.violationStatesTemplate = {}
          this.violationStatesTemplate.category = this.statesTemplate.category
          this.violationStatesTemplate.state = this.statesTemplate.state
          this.violationStatesTemplate.location = this.statesTemplate.location
          if (this.stateUpdateFlag) {
            this.states[this.index] = this.statesTemplate
            this.violationStates[this.index] = this.violationStatesTemplate
          } else {
            this.states.push(this.statesTemplate)
            this.violationStates.push(this.violationStatesTemplate)
          }
          this.cancelAddState()
          this.statesTemplate = {
            category: '',
            state: '',
            location: '',
            value: ''
          }
          this.violationStatesTemplate = {
            category: '',
            state: '',
            location: '',
            value: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    statesChange (val) {
      this.statesTemplate.value = null
      if (this.statesTemplate.state) {
        this.valueOptions = this.STATE.state[val]
      } else {
        this.valueOptions = []
      }
    },

    /*
    cancelAddCondition () {
      this.conditionDialog = false
      this.refresh()
    },
    confimAddCondition () {
      this.$refs.conditionsTemplate.validate((valid) => {
        if (valid) {
          if (!this.conditionsTemplate.conditionTimeFlag && !this.conditionsTemplate.conditionStatesFlag) {
            this.$message.warning('Please choose at least one of State and Time!')
            return
          }
          if (this.conditionsTemplate.conditionStatesFlag && this.conditionsTemplate.state === '') {
            this.$message.warning('Please select more than one state!')
            return
          }
          if (this.conditionsTemplate.way === 'After' || this.conditionsTemplate.way === 'Before') {
            if (this.conditionsTemplate.conditionTimeFlag && ((this.conditionsTemplate.conditionStatesFlag && this.conditionsTemplate.timeUnit === '') || (!this.conditionsTemplate.conditionStatesFlag && (this.conditionsTemplate.time === '' || this.conditionsTemplate.time === null)))) {
              this.$message.warning('Please fill in the Time field of the form correctly!')
              return
            }
          } else if (this.conditionsTemplate.way === 'Last') {
            if (this.conditionsTemplate.timeUnit === '' || this.conditionsTemplate.time === '' || this.conditionsTemplate.time === null) {
              this.$message.warning('Please fill in the Time field of the form correctly!')
              return
            }
          }
          this.conditionDialog = false

          if (this.conditionUpdateFlag) {
            this.conditions[this.conditionIndex] = this.conditionsTemplate
          } else {
            this.conditions.push(this.conditionsTemplate)
          }

          this.cancelAddCondition()
          this.conditionsTemplate = {
            way: '',
            state: '',
            time: '',
            timeUnit: '',
            conditionStatesFlag: false,
            conditionTimeFlag: false
          }
          this.conditionTimeFlag = false
          this.conditionStatesFlag = false
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    addCondition () {
      this.conditionDialog = true
    },
    wayChange (val) {
      if (val === 'While') {
        this.conditionsTemplate.conditionStatesFlag = true
        this.conditionsTemplate.conditionTimeFlag = false
      } else if (val === 'Last') {
        this.conditionsTemplate.conditionStatesFlag = false
        this.conditionsTemplate.conditionTimeFlag = true
      } else {
        this.conditionsTemplate.conditionStatesFlag = false
        this.conditionsTemplate.conditionTimeFlag = false
      }
      this.conditionsTemplate.state = ''
      this.conditionsTemplate.time = ''
      this.conditionsTemplate.timeUnit = ''
    },
    removeConditionItem (conditionIndex) {
      this.conditions.splice(conditionIndex, 1)
      this.refresh()
    },
    updateConditionItem (Info, conditionIndex) {
      if (Info && conditionIndex >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.conditionDialog = true
        this.conditionsTemplate = item

        console.log(item)
        this.conditionIndex = conditionIndex
        this.conditionUpdateFlag = true
      }
    },
    editConditionState () {
      const states = this.conditionsTemplate.state.split(' & ')
      this.conditionStates = []
      if (states[0] !== '') {
        for (let i = 0; i < states.length; i++) {
          console.log(states[i])
          const temp = states[i].split('.')
          this.conditionStates.push({
            location: temp[0],
            category: temp[1],
            state: temp[2],
            value: temp[3]
          })
        }
      }
      console.log(this.conditionStates)
      this.conditionStatesDialog = true
    },
    addConditionState () {
      this.conditionStateDialog = true
      this.conditionStateUpdateFlag = false
    },
    conditionChooseState (val) {
      this.conditionsTemplate.conditionTimeFlag = false
      this.conditionsTemplate.time = ''
      this.conditionsTemplate.timeUnit = ''
      this.conditionsTemplate.state = ''
      this.conditionStates = []
    },
    conditionChooseTime (val) {
      this.conditionsTemplate.time = ''
      this.conditionsTemplate.timeUnit = ''
    },

    // 修改condition的states集合
    cancelEditConditionStates () {
      this.conditionStatesDialog = false
    },
    confimEditConditionStates () {
      if (this.conditionStates.length === 0) {
        this.$message.warning('Please fill in at least one state!')
        return
      }
      this.conditionsTemplate.state = this.conditionStates[0].location + '.' + this.conditionStates[0].category + '.' + this.conditionStates[0].state + '.' + this.conditionStates[0].value
      for (let i = 1; i < this.conditionStates.length; i++) {
        this.conditionsTemplate.state = this.conditionsTemplate.state + ' & ' + this.conditionStates[i].location + '.' + this.conditionStates[0].category + '.' + this.conditionStates[i].state + '.' + this.conditionStates[i].value
      }
      this.conditionStatesDialog = false
      this.conditionStates = []
    },
    updateConditionStateItem (Info, conditionStateIndex) {
      if (Info && conditionStateIndex >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.conditionStateDialog = true
        this.statesTemplate = item
        this.inquiryInfor(this.statesTemplate.category)
        this.statesChange(this.statesTemplate.state)
        this.conditionStateIndex = conditionStateIndex
        this.conditionStateUpdateFlag = true
      }
    },
    removeConditionStateItem (conditionStateIndex) {
      this.conditionStates.splice(conditionStateIndex, 1)
      this.refresh()
    },

    // 修改condition的某个state
    confimAddConditionState () {
      this.$refs.statesTemplate.validate((valid) => {
        if (valid) {
          if (this.statesTemplate.value === '' || this.statesTemplate.value === null) {
            this.$message.warning('Please fill in the form correctly!')
            return
          }
          this.conditionStateDialog = false
          if (this.conditionStateUpdateFlag) {
            this.conditionStates[this.conditionStateIndex] = this.statesTemplate
          } else {
            this.conditionStates.push(this.statesTemplate)
          }
          this.cancelAddConditionState()
          this.statesTemplate = {
            category: '',
            state: '',
            location: '',
            value: ''
          }
        } else {
          this.$message.warning('Please fill in the form correctly!')
        }
      })
    },
    cancelAddConditionState () {
      this.conditionStateDialog = false
      this.statesTemplate = {
        category: '',
        state: '',
        location: '',
        value: ''
      }
    },
    */

    getLocation () {
      const that = this
      this.locationOptions = []
      axios.get('/' + this.URL.url + '/room_list', {
        headers: {
          'Content-Type': 'application/text'
        }
      }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          that.locationOptions.push({
            value: res.data[i],
            label: res.data[i]
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    checkUpload () {
      if (this.states.length < 1) {
        this.$message.warning('Please fill in at least one state!')
        return
      }
      this.uploadDialogVisible = true
    },
    upload () {
      this.TIME.timeFlag = false
      const that = this
      this.$refs.form.validate((valid) => {
        if (valid) {
          that.uploadDialogVisible = false
          const tempTime = new Date()
          let object = this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
          for (let i = 1; i < this.states.length; i++) {
            object = object + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
          }
          let fix = ""
          for (let i = 0; i < this.violationStates.length; i++) {
            if (this.violationStates[i].value != "" && this.violationStates[i].value != undefined)
              fix = fix + this.violationStates[i].location + '.' + this.violationStates[i].category + '.' + this.violationStates[i].state + '.' + this.violationStates[i].value + " & "
          }
          if (fix.endsWith(' & ')) {
            fix = fix.slice(0, -3);
          }
          this.propertyList.push(
            {
              object: object,
              // frequency: this.radio,
              fix: fix
              //condition: this.conditions
            }
          )
          axios({
            url: '/upload/saveByTemplate/' + this.URL.param,
            method: 'POST',
            data: {
              app: that.form.name,
              dsl: that.propertyList,
              time: tempTime.getFullYear() + '/' + (tempTime.getMonth() + 1) + '/' + tempTime.getDate(),
              // time: (new Date().getTime() - that.TIME.time) / 1000,
              user: that.form.user
            }
          }).then((response) => {
            that.propertyList = []
            that.form = {
              name: '',
              user: '',
            }
            this.$router.push({
              name: 'HomeView'
            })
          })
        }
      })
    },
    Confirm () {
      console.log("*******")
      console.log(this.violationStates)
      if (this.states.length < 1) {
        this.$message.warning('Please fill in at least one state!')
        return
      }
      let object = this.states[0].location + '.' + this.states[0].category + '.' + this.states[0].state + '.' + this.states[0].value
      for (let i = 1; i < this.states.length; i++) {
        object = object + ' & ' + this.states[i].location + '.' + this.states[i].category + '.' + this.states[i].state + '.' + this.states[i].value
      }
      let fix = ""
      for (let i = 0; i < this.violationStates.length; i++) {
        if (this.violationStates[i].value != "" && this.violationStates[i].value != undefined)
          fix = fix + this.violationStates[i].location + '.' + this.violationStates[i].category + '.' + this.violationStates[i].state + '.' + this.violationStates[i].value + " & "
      }
      if (fix.endsWith(' & ')) {
        fix = fix.slice(0, -3);
      }
      this.propertyList.push(
        {
          object: object,
          // frequency: this.radio,
          fix: fix
          //condition: this.conditions
        }
      )
      this.$router.back()
    },
    // 更新界面
    refresh () {
      this.$forceUpdate()
    }
  }
}
</script>

