<template>
  <el-container class="container">
    <el-main>
      <h1 class="home-title">Application Description</h1>
      <div style="display: flex;flex-direction: row-reverse;">
        <el-button type="primary" @click="checkUpload" style="margin-right: 30px;margin-bottom: 10px;">UPLOAD</el-button>
      </div>
      <el-dialog width="70%" title="UPLOAD CONFIGURATION" :visible.sync="uploadDialogVisible">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="Name" prop="name">
            <el-input placeholder="Please input the name of the violation description" v-model="form.name" clearable>
            </el-input>
          </el-form-item>
          <el-form-item label="User" prop="user">
            <el-input placeholder="Please input your nickname" v-model="form.user" clearable>
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="uploadDialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="upload">Confirm</el-button>
        </span>
      </el-dialog>
      <!-- list 配置结果-->
      <div v-for="(t, i) in propertyList" :key="i">
        <el-card class="card-margin">
          <el-descriptions :labelStyle="{'width': '15%','font-size': '11pt'}" :title="'Property ' + (i + 1)" :column="1" border>
            <template slot="extra">
              <el-button size="mini" icon="el-icon-edit" @click="updateItem(t, i)">
              </el-button>
              <el-button size="mini" icon="el-icon-delete" @click="removeItem(i)"></el-button>
            </template>
            <!-- <el-descriptions-item label="Abstract Level">{{ t.template }}</el-descriptions-item> -->
            <el-descriptions-item label="Object">{{ t.object }}</el-descriptions-item>
            <el-descriptions-item label="Fix">{{ t.fix }}</el-descriptions-item>
            <!--
            <el-descriptions-item label="Filter">
              <div v-for="(c, j) in t.condition" :key="j">
                <el-descriptions :labelStyle="{'width': '5%','font-size': '11pt'}" :column="1" border>
                  <el-descriptions-item :label="j + 1" v-if="c.state !== '' && c.time !== ''">{{ c.way + ' ' +  c.state + ' & ' + c.time + ' ' + c.timeUnit}}</el-descriptions-item>
                  <el-descriptions-item :label="j + 1" v-else-if="c.state === '' && c.time !== ''">{{ c.way + ' ' + c.time + ' ' + c.timeUnit }}</el-descriptions-item>
                  <el-descriptions-item :label="j + 1" v-else-if="c.state !== '' && c.time === ''">{{ c.way + ' ' +  c.state }}</el-descriptions-item>
                </el-descriptions>
              </div>
            </el-descriptions-item>
            <el-descriptions-item label="ltl" v-if="t.ltl">{{ t.ltl }}</el-descriptions-item> 
            -->
          </el-descriptions>
        </el-card>
      </div>
      <!-- 添加按钮和配置对话框 -->
      <el-card style="display: flex;align-content: center;justify-content: center;" class="card-margin">
        <el-button type="text" @click="addItem()">
          <i class="el-icon-circle-plus" style="font-size: 25px;"> Add Property</i>
        </el-button>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'

export default {
  name: 'addApplication',
  components: {
  },
  data () {
    return {
      enviroment: {
        enviroment: ''
      },
      beginFlag: false,
      propertyList: [],
      form: {
        name: '',
        user: '',
      },
      rules: {
        name: [
          { required: true, message: 'Please input the name of the violation description!', trigger: 'blur' }
        ],
        user: [
          { required: true, message: 'Please input your nickname!', trigger: 'blur' }
        ]
      },

      uploadDialogVisible: false
    }
  },
  methods: {
    // 修改property
    updateItem (Info, index) {
      if (Info && index >= 0) {
        const item = JSON.parse(JSON.stringify(Info))
        this.$router.push({
          name: 'addViolationRules',
          params: {
            update: index,
            value: item
          }
        })
      }
    },
    removeItem (index) {
      this.propertyList.splice(index, 1)
      this.refresh()
    },
    addItem () {
      if (this.beginFlag === false) {
        this.beginFlag = true
        this.TIME.time = new Date().getTime()
      }
      this.update = -1
      this.$router.push({
        name: 'addViolationRules',
        params: {
          update: -1,
          value: ''
        }
      })
    },
    cancelAddItem () {
      this.refresh()
    },

    refresh () {
      this.$forceUpdate()
    },
    checkUpload () {
      this.uploadDialogVisible = true
      if (this.propertyList.length < 1) {
        this.$message.warning('There are no properties to upload!')
        this.uploadDialogVisible = false
      }
    },
    upload () {
      this.beginFlag = false
      const that = this
      this.$refs.form.validate((valid) => {
        if (valid) {
          that.uploadDialogVisible = false
          const tempTime = new Date()
          axios({
            url: '/upload/saveByTemplate/' + this.URL.param,
            method: 'POST',
            data: {
              app: that.form.name,
              dsl: that.propertyList,
              time: tempTime.getFullYear() + '/' + (tempTime.getMonth() + 1) + '/' + tempTime.getDate(),
              // time: (new Date().getTime() - that.TIME.time) / 1000,
              user: that.form.user
            }
          }).then((response) => {
            that.propertyList = []
            that.form = {
              name: '',
              user: '',
            }
            this.$router.push({
              name: 'HomeView'
            })
          })
        }
      })
    }
  },
  mounted () {
    // this.PROPERTY.property = this.$route.params.value.dsl
    // this.form.name = this.$route.params.value.app
    // this.form.user = this.$route.params.value.user
  }
}
</script>

<style scoped>
.home-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.property {
  height: 100%;
  width: 100%;
}

.card-margin {
  margin: 10px;
}

.space-title,
.time-title {
  text-align: center;
  font-size: 18pt;
  margin-bottom: 15px;
  font-weight: bold;
  width: 91%;
  margin-bottom: 15px;
  padding: 15px 2.5%;
  letter-spacing: 2px;
  border-radius: 10px;
  border: 1px solid rgb(234, 229, 229);
  color: white;
}

.space-title {
  background-color: rgb(43, 195, 219);
}

.time-title {
  background-color: rgb(152, 210, 98);
}
</style>
