<template>
  <el-row :gutter='5' type="flex" style="height:100%;width:100%;">
    <el-col :span='4.5'>
      <div class="app-container">
        <el-menu :default-active="URL.page" style="height:100%;" background-color="#555555" text-color="#fff" active-text-color="#ffd04b">
          <el-menu-item index="myEnvironment" @click="changeMenu('myEnvironment')">
            <i class="el-icon-place"></i>
            <span slot="title">My Environment</span>
          </el-menu-item>
          <el-menu-item index="violationList" @click="changeMenu('violationList')">
            <i class="el-icon-document"></i>
            <span slot="title">Violation Rule</span>
          </el-menu-item>
        </el-menu>
      </div>
    </el-col>
    <el-col :span='21'>
      <div :is="URL.page"></div>
    </el-col>
    <el-dialog title="CHOOSE ENVIRONMRNT" top="10%" width="40%" :visible.sync="URL.dialog" :showClose="false" :close-on-click-modal="false">
      <el-row>
        <el-col :span='12'>
          <el-row style="display: flex;justify-content: center;">
            <el-button plain round style="padding:30px;margin-bottom:20px;font-size:16pt;color:white;background-color:rgb(100, 195, 219)" @click="chooseEnvironment('Smart Office')">
              Smart Office
            </el-button>
          </el-row>
        </el-col>
        <el-col :span='12'>
          <el-row style="display: flex;justify-content: center;">
            <el-button plain round style="padding:30px;margin-bottom:20px;font-size:16pt;color:white;background-color:rgb(242, 185, 80)" @click="chooseEnvironment('Smart Home')">
              Smart Home
            </el-button>
          </el-row>
        </el-col>
      </el-row>
    </el-dialog>
  </el-row>
</template>

<script>
import myEnvironment from '../components/myEnvironment.vue'
import violationList from '../components/violationList.vue'

export default {
  name: 'HomeView',
  components: {
    myEnvironment,
    violationList
  },
  data () {
    return {
      page: this.URL.page
    }
  },
  methods: {
    changeMenu (menu) {
      this.URL.page = menu
      this.$forceUpdate()
    },
    chooseEnvironment (enviroment) {
      this.URL.dialog = false
      this.changeMenu('myEnvironment')
      if (enviroment === 'Smart Office') {
        this.URL.url = 'labapi'
        this.URL.param = 'labEvaluation'
      } else {
        this.URL.url = 'homeapi'
        this.URL.param = 'homeEvaluation'
      }
      this.$forceUpdate()
    }
  }
}
</script>

<style lang="scss">
.container {
  height: 100%;
  width: 100%;
}

.app-container {
  width: 100%;
  height: 100%;
}

.el-menu-item {
  margin-top: 15%;
  margin-bottom: 20%;
  height: 8%;
  width: 100%;
  font-size: 12pt !important;
}
</style>
